import { Link } from "react-router-dom"

const NavBar = () => (
  <nav>
    <ul className="nav-ul">
      <li>
        <Link className="nav-link" to="/">Home</Link>
        <Link className="nav-link" to="/about">About</Link>
        <Link className="nav-link" to="/articles-list">Articles</Link>
      </li>
    </ul>
  </nav>
)

export default NavBar
